.img-container {
  border: 1px solid brown;
  margin: 0 auto;
  width: 100%;

}

.img_item {
  max-height: 150px;
  width: 90%;
  padding: 10px;
  margin: 0 auto;
  display: flex;

}


.buttons-container {
  border: 3px solid seagreen;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.ownerButton {
  background: aliceblue;
  outline-style: none;
}

.renterButton {
  background: lightgreen;
  outline-style: none;
}

.avail {
  font-size: 14px;
  color: green;
}

.rented {
  font-size: 12px;
  color: blue;
}